import Vue from 'vue';
import VueTheMask from 'vue-the-mask'
import Bowser from 'Bowser'
import SlideUpDown from 'vue-slide-up-down'
import Sprite from '../components/Sprite.vue'
import HeaderMain from '../components/HeaderMain.vue'
import MainPage from '../components/MainPage.vue'
import History from '../components/History.vue'
import Epoque from '../components/Epoque.vue'
import Popularity from '../components/Popularity.vue'
import Skyscraper from '../components/Skyscraper.vue'
import Neighbours from '../components/Neighbours.vue'
import Theater from '../components/Theater.vue'
import FooterMain from '../components/FooterMain.vue'
import Introduction from '../components/Introduction.vue'
import Socials from '../components/Socials.vue'
import Next from '../components/Next.vue'
import Statue from '../components/Statue.vue'

import vClickOutside from 'v-click-outside'

Vue.use(vClickOutside)

import VueYouTubeEmbed from "vue-youtube-embed";
import Helpers from '../lib/Helpers.js'

Vue.use(VueTheMask)
Vue.use(Helpers)
Vue.component('slide-up-down', SlideUpDown)


Vue.use(VueYouTubeEmbed)


Vue.config.comments = true
Vue.config.ignoredElements = [
  'noindex',
  /^ion-/
]

const app = new Vue({
  el: '#app',
  data() {
    return {
      window_top: 0,
      header_top: 0,
      header_height: 183,
      tabletBreakpoint: 1025,
      // mobileBreakpoint: 767,
      mobileBreakpoint: 600,
      menuShown: false,
      width: null,
      height: null,
      bowser: null,
      ym_counter: null,
      recaptcha_key: null,
    }
  },
  watch: {},
  async created() {
    this.width = window.innerWidth;
    this.height = window.innerHeight;
    // this.bowser = Bowser.parse(window.navigator.userAgent);
  },
  computed: {
    isTablet() {
      return this.width <= this.tabletBreakpoint;
    },
    isMobile() {
      return this.width <= this.mobileBreakpoint;
    }
  },
  mounted() {
    this.addListeners();
  },
  methods: {
    addListeners() {
      window.addEventListener('scroll', this.onScroll);
      window.addEventListener('resize', this.onResize);
      window.onload = () => {}
    },
    onScroll(e) {
      this.window_top = window.pageYOffset;
    },
    onResize() {
      this.width = window.innerWidth;
      this.height = window.innerHeight;
      this.menuShown = false;
    },
    reachGoal(goal) {
      if (typeof ym !== 'undefined') {
        ym(this.ym_counter, 'reachGoal', goal);
        console.log(goal);
      }
    },
    getRecaptchaToken(action) {
      return new Promise((resolve, reject) => {
        if (typeof grecaptcha === 'undefined') {
          return resolve(false);
        }
        grecaptcha.ready(() => {
          grecaptcha.execute(this.recaptcha_key, {
            action: action
          }).then((token) => {
            return resolve(token);
          }, (error) => {
            console.log(error);
            return resolve(false);
          });
        })
      })
    },
  },
  components: {
    Sprite,
    HeaderMain,
    MainPage,
    History,
    Epoque,
    Introduction,
    Theater,
    Popularity,
    Neighbours,
    FooterMain,
    Skyscraper,
    Socials,
    Next,
    Statue
  }
})