<template>
  <div class="popularity__inner">
    <div class="container">
      <div class="popularity__persons">
        <div
          class="person"
          v-for="(person, i) in persons"
          :key="i"
          :class="person.class"
        >
          <template v-if="person.type !== 'last'">
            <span class="person__name">{{ person.name }}</span>
            <div class="person__content">
              <div class="person__pic-wrapper">
                <div class="person__pic">
                  <img
                    :src="person.src"
                    :alt="person.alt"
                    :width="person.width"
                    :height="person.height"
                  />
                </div>
              </div>
              <div class="person__texts">
                <h3>{{ person.title }}</h3>
                <p class="person__text">{{ person.text }}</p>
                <p class="person__quote">{{ person.quote }}</p>
              </div>
            </div>
          </template>
          <template v-else>
            <div class="person__texts">
              <h3>{{ person.title_desktop }}</h3>
              <p class="person__text">{{ person.text }}</p>
              <p class="person__quote">{{ person.quote }}</p>
            </div>

            <div class="person__pic-wrapper">
              <div class="person__pic">
                <img
                  :src="person.src"
                  :alt="person.alt"
                  :width="person.width"
                  :height="person.height"
                />
              </div>
              <span class="person__name">{{ person.name }}</span>
            </div>
            <p class="person__text-additional">
              {{ person.text_additional }}
              <a
                class="button button--round button--epoque button--person"
                href="#"
              >
                <svg class="icon" width="18" height="18" aria-hidden="true">
                  <use xlink:href="#arrow"></use>
                </svg>
                <span>
                  {{ search_text_desktop }}
                </span>
              </a>
            </p>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Persons",
  components: {},
  data() {
    return {
      title: "Звездное место",
      title_desktop: "Важнейшее из искусств",
      search_text: "Искать на Авито",
      search_text_desktop: "Попасть в зазеркалье",
      main_text:
        "На крыше дома Нирнзее снимались эпизоды фильмов «Служебный роман», «Курьер» и «Сказки... Сказки… Сказки старого Арбата».",
      persons: [
        {
          name: "М. Булгаков",
          src: "img/bulgakov.jpg",
          alt: "Булгаков",
          width: "200",
          height: "280",
          class: "person--even",
          title: "Мастер и Маргарита",
          text:
            "В этом доме Михаил Булгаков встретился с будущей женой Еленой Шиловской, которая стала прототипом Маргариты.",
        },
        {
          name: "В. Маяковский",
          src: "img/mayakovski.jpg",
          alt: "Маяковский",
          width: "200",
          height: "280",
          class: "person--odd person--last",
          title: "Послания к великим",
          title_desktop: "Осколки чувств",
          text:
            "В 90-х рабочие случайно разбили зеркало в парадной и нашли за ним записку от Софьи Шамардиной к Владимиру Маяковскому.",
          quote:
            "«Люблю зеркало. Хочу сегодня быть красивой. Я очень люблю тебя, Владимир Маяковский. Стихи твои  и тебя. Сонка»",
          text_additional:
            "В поисках особенных зеркал досоветского и раннесоветского периодов загляните на Авито.",
          type: "last",
        },
      ],
    };
  },
  computed: {},
  mounted() {},
  methods: {},
  beforeDestroy() {},
};
