<template>
  <header class="header" :style="{ top: $root.header_top + 'px' }">
    <div class="container">
      <div class="header__label" v-if="$root.width >= 1024">
        <a :href="label.href"> </a>
      </div>
      <div class="header__label" v-if="$root.width <= 1023">
        <a :href="label.href">
          <svg
            class="icon"
            :width="label.icon_width"
            :height="label.icon_height"
            aria-hidden="true"
          >
            <use xlink:href="#arrow_up"></use>
          </svg>
          <span>{{ label.city }}</span>
        </a>
      </div>
      <div class="header__top">
        <div class="header__logo">
          <a :href="'home.html'" class="header__logo-link">
            <img
              loading="lazy"
              :src="logo.logo_src"
              alt="Логотип Авито"
              :width="logo.logo_width"
              :height="logo.logo_height"
            />
          </a>
        </div>

        <ul class="header__menu" v-if="!$root.isTablet">
          <li class="header__menu-item" v-for="(menu, i) in menu" :key="i">
            <a :href="menu.link"> {{ menu.name }}</a>
          </li>
        </ul>
      </div>
      <button
        v-if="$root.isTablet"
        class="button button--toggle"
        @click="menuToggle()"
        :class="{ close: !menuShown }"
      >
        <span></span>
      </button>
    </div>
    <transition name="fade">
      <div
        class="header__mobile-wrapper"
        v-if="$root.isTablet"
        v-show="menuShown"
      >
        <div class="header__overlay" @click="menuClose()"></div>
        <div class="header__mobile">
          <div class="container">
            <ul class="header__menu">
              <li class="header__menu-item" v-for="(menu, i) in menu" :key="i">
                <a :href="menu.link" @click="menuClose()"> {{ menu.name }}</a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </transition>
  </header>
</template>

<script>
export default {
  data() {
    return {
      menuShown: false,
      label: {
        href: "#",
        city: "Москва",
        icon_width: "11",
        icon_height: "9",
      },
      logo: {
        logo_src: "img/Logo_Avito.png",
        logo_width: 141,
        logo_height: 40,
        // logo_width_mob: 64,
        // logo_height_mob: 35,
      },
      menu: [
        {
          name: "Музеи",
          link: "#museums",
        },
        {
          name: "Подкасты",
          link: "#podcasts",
        },
        {
          name: "Статьи",
          link: "#articles",
        },
        {
          name: "О проекте",
          link: "#about",
        },
      ],
    };
  },
  computed: {},
  mounted() {
    window.addEventListener("resize", this.handleResize);
    this.toggleMobile();
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.handleResize);
    this.toggleMobile();
  },
  methods: {
    menuToggle() {
      this.menuShown = !this.menuShown;
      document.body.classList.toggle("scroll-off");
    },
    menuClose() {
      if (this.menuShown) {
        this.menuShown = false;
        document.body.classList.remove("scroll-off");
      }
    },
    toggleMobile() {
      if (window.innerWidth <= this.mobileBreakpoint) {
        this.isMobile = true;
      } else {
        this.isMobile = false;
      }
    },
    // handleScroll() {
    //   this.menuClose()
    // },
    handleResize() {
      this.toggleMobile(), this.menuClose();
    },
  },
  created() {
    document.addEventListener("keyup", (evt) => {
      if (evt.keyCode === 27) {
        this.menuClose(true);
      }
    });
  },
  components: {},
};
</script>
