<template>
  <section class="history">
    <div class="container" v-if="!$root.isMobile">
      <div class="history__wrapper">
        <h2 class="history__title">{{ title }}</h2>
        <div class="history__inner">
          <div class="history__info">
            <div class="history__texts">
              <p v-for="(text, i) in house_texts_desktop" :key="i">
                {{ text.text }}
              </p>
            </div>
            <div class="history__plan">
              <img :src="plan.src_plan" :alt="plan.alt_plan" />
            </div>
          </div>

          <div class="history__details">
            <div class="history__video">
              <img :src="house.src_house" :alt="house.alt_house" />
              <button
                type="button"
                @click="openVideo()"
                class="button button--play button--play-big"
              ></button>
            </div>
            <div class="history__house-text">
              <h3>{{ note_title }}</h3>
              <p>{{ house_text }}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="history__wrapper" v-else>
      <div class="history__inner">
        <div class="container">
          <div class="history__info">
            <div class="history__texts">
              <p>
                {{ house_text_mob }}
              </p>
            </div>
          </div>
        </div>

        <div class="history__details">
          <div class="history__video">
            <img :src="house.src_house" :alt="house.alt_house" />
            <button
              type="button"
              @click="openVideo()"
              class="button button--play button--play-big"
            >
              <svg
                class="icon"
                :width="icon_play.width"
                :height="icon_play.height"
                aria-hidden="true"
              >
                <use xlink:href="#play-big"></use>
              </svg>
            </button>
          </div>
          <div class="container">
            <div class="history__house-text">
              <h3>{{ note_title_mob }}</h3>
              <p v-for="(text, index) in house_texts_mob" :key="index">
                {{ text.text }}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <template v-if="isPopupOpened">
      <div class="modal" v-click-outside="onClickOutside">
        <div
          class="modal__overlay"
          v-show="isPopupOpened"
          @click="closeAllPopups()"
        >
          <div class="modal__body">
            <youtube
              :video-id="videoId"
              :player-width="width"
              :player-height="!$root.isMobile ? height : heightMobile"
              :allowfullscreen="allowfullscreen"
            ></youtube>
          </div>
        </div>
      </div>
    </template>
  </section>
</template>

<script>
export default {
  name: "history",
  components: {},
  data() {
    return {
      isPopupOpened: false,

      mobileBreakpoint: 1024,
      windowWidth: window.innerWidth,
      isMobile: false,
      videoId: "rK_TFhRCPPM",
      width: "100%",
      height: 770,
      heightMobile: 400,
      allowfullscreen: 0,
      title: "История",
      note_title: "Эпицентр культурной жизни",
      note_title_mob: "Ближе к легенде",
      icon_play: {
        width: 54,
        height: 61,
      },
      icon_play_big: {
        width: 79,
        height: 90,
      },
      star: {
        src_star: "img/star.png",
        alt_star: "звезда",
        width: "180",
        height: "180",
      },
      plan: {
        src_plan: "img/plan.png",
        alt_plan: "План",
        width: "450",
        height: "318",
      },
      house: {
        // src_house: "img/video.jpg",
        src_house: "img/house.png",
        alt_house: "Дом",
        width: "864",
        height: "485",
      },
      house_text:
        "Дом Нирнзее примечателен не только своей высотой и талантливыми жильцами. Во все времена он умудрялся привлекать москвичей необычными активностями. Больше века назад можно было послушать «прожарку» театральных деятелей в подвале дома, а сейчас — познакомиться с живым бараном Толиком, талисманом колоритного грузинского кафе, на первом этаже. Чем ещё компенсировалась скромная площадь квартир? Узнаете из видео. ",
      house_texts_desktop: [
        {
          text:
            "Дом Нирнзее называли «домом холостяков» — всё из-за небольшой площади квартир. Они особенно привлекали творческих личностей, студентов и одиночек.",
        },
        {
          text:
            "Здесь с простыми людьми соседствовали Владимир Маяковский и Михаил Булгаков, московская богема и партийная элита. Это место с неповторимой атмосферой, где можно найти легендарную рукопись, встретить живого барана или сняться в кино.",
        },
      ],

      house_text_mob:
        "Расположенный в двух шагах от Пушкинской площадки дом Нирнзее называли «домом холостяков» — всё из-за небольшой площади квартир. Малогабаритное жильё особенно привлекало творческих личностей и одиночек. ",
      house_texts_mob: [
        {
          text:
            "Дом Нирнзее свое время он стал настоящим шедевром урбанистики. Как высотка совместила в себе всё, что нужно москвичу? Узнаете из видео! ",
        },
        {
          text:
            "Крыша дома Нирнзее — одна из самых примечательных в Москве. Она использовалась   как ресторан, каток, футбольное поле, кинотеатр    и кинопавильон, а также компенсировала нехватку места во дворе. Сейчас на крыше располагается редакция «Вопросы литературы».  ",
        },
      ],
    };
  },
  computed: {},
  mounted() {},
  methods: {
    openVideo() {
      this.isPopupOpened = true;
    },
    closeAllPopups() {
      this.isPopupOpened = false;
    },
    onClickOutside () {
      this.closeAllPopups()
    }
  },
  beforeDestroy() {},
  created() {
    let that = this;
    document.addEventListener("keyup", function(evt) {
      if (evt.keyCode === 27) {
        that.closeAllPopups();
      }
    });
  },
};
</script>
